import "./Font.css";
import { useRef } from "react";

const Font = (props) => {
    let fontRef = useRef();

    const selectionHandler = (e) => {
        props.selectionChange(fontRef.current.value);
    };

    const Fonts = ["Indie Flower", "Kalam", "Popsies", "Just Another Hand"];

    const options = Fonts.map((font) => {
        const Style = {
            "fontFamily": font,
        }
        return (
            <option key={font} value={font} style={Style}>
                {font}
            </option>
        );
    });

    return (
        <div className="font">
            <h1>Select Font: </h1>
            <select
                onChange={selectionHandler}
                ref={(f) => (fontRef.current = f)}
            >
                {options}
            </select>
        </div>
    );
};

export default Font;
