import "./Step.css";
import * as html2canvas from "html2canvas";

const Step = (props) => {
    // console.log(props);
    const val = props.val;
    const { title, description, color } = val;

    const downloader = (div) => {
        html2canvas(div,{
            // allowTaint: true,
            // foreignObjectRendering: true,
            useCORS: true,
        }).then(function (canvas) {
            var anchorTag = document.createElement("a");
            document.body.appendChild(anchorTag);
            document.getElementById("history").appendChild(canvas);
            // console.log(canvas);
            
            anchorTag.download = "filename.jpg";
            anchorTag.crossOrigin = "anonymous";
            anchorTag.href = canvas.toDataURL("image/jpeg").replace("image/jpeg", "image/octet-stream");
            // anchorTag.href = canvas.toDataURL();
            anchorTag.target = "_blank";
            anchorTag.click();
        });
    };


    const clickHandler = (e) => {
        // console.log("clicked");
        if(e.target.id === "Preview") {
            const r = document.getElementById("Result__text");
            const text = document.getElementById("text").value;
            r.innerText = text;
        }
        else if(e.target.id === "Download") {
            downloader(document.getElementById("Result"));
        }
    };

    return (
        <div className="step">
            <h1>{title}</h1>
            <button color={color} id={description} onClick={clickHandler}>
                {description}
            </button>
        </div>
    );
};

export default Step;
