import Step from "./Step";
import "./Steps.css";

const Steps = (props) => {
    const steps = Object.keys(props.val).map((key) => {
        return <Step key={key} val={props.val[key]} />;
    });

    return (
        <div className="steps">
            <div className="steps__wrapper">{steps}</div>
        </div>
    );
};

export default Steps;
