import "./Result.css";
import image from "../assets/page.jpg";
import { useEffect } from "react";

const Result = (props) => {
    console.log(props);
    useEffect(() => {
        document.getElementById("Result__text").style.fontFamily = props.currentFont;
    }, [props.currentFont]);
    return (
        <div className="Result" id="Result">
            <img src={image} alt="page" />
            <div id="Result__text" currentFont={props.currentFont}>
                Enter your notes in the box above and click on the preview button to see the result.
<br />
                Click on the download button to download the result.
<br />
                ;)
            </div>
        </div>
    );
};

export default Result;
